*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: #f1f5f8;
    color: #222;
}

.booklist {
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto ;
    display: grid;
    gap: 2rem;
}

@media screen and (min-width:768px) {
    .booklist{
        grid-template-columns: repeat(3,1fr);
        align-items: self-start;
    }
}
.book{
    background: #fff;
    border-radius: 2rem;
    padding: 2rem;
    text-align: center;
}

.book img{
    width: 100%;
    object-fit: cover;
}
.book h2{
    margin-top: 1rem;
    font-size: 1rem;
}